import styled from "@emotion/styled";
import { Add, Remove } from "@mui/icons-material";
import Announcement from "../components/Announcement";
import Footer from "../components/Footer";
import Navbar from "../components/Navbar";
import Newsletter from "../components/Newsletter";

const Container = styled.div``;
const Wrapper = styled.div`
    padding: 50px;
    display: flex;
`;
const ImgContainer = styled.div`
    flex: 1;
`;
const Image = styled.img`
    width: 100%;
    object-fit: cover;
`;
const InfoContainer = styled.div`
    flex: 1;
    padding: 0px 50px;
`;
const Title = styled.h1`
    font-weight: 200;
`;
const Description = styled.p`
    margin: 20px 0;
`;
const Price = styled.span`
    font-weight: 100;
    font-size: 40px;
`;
const FilterContainer = styled.div`
    width: 50%;
    margin: 30px 0;
    display: flex;
    justify-content: space-between;
`;
const Filter = styled.div`
    display: flex;
    align-items: center;
`;
const FilterTitle = styled.span`
    font-size: 20px;
    font-weight: 200;
`;
const FilterFlavor = styled.div`
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: ${(props) => props.flavor};
    margin: 0 5px;
    cursor: pointer;
`;
const FilterSize = styled.select`
    margin-left: 10px;
    padding: 5px;
`;
const FilterSizeOption = styled.option``;
const AddContainer = styled.div`
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: space-between;
`;
const AmountContainer = styled.div`
    display: flex;
    align-items: center;
    font-weight: 700;
`;
const Amount = styled.span`
    width: 30px;
    height: 30px;
    border-radius: 10px;
    border: 1px solid teal;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0px 5px;
    font-size: 18px;
`;

const Button = styled.button`
    margin-left: 20px;
    width: 600px;
    padding: 15px;
    border: 2px solid teal;
    border-radius: 10px;
    background: white;
    color: teal;
    font-weight: 500;
    font-size: 18px;
    cursor: pointer;

    &:hover {
        background: teal;
        color: white;
    }
`;

const Product = () => {
    return (
        <Container>
            <Navbar />
            <Announcement />
            <Wrapper>
                <ImgContainer>
                    <Image src="https://www.llaollaoweb.com/wp-content/uploads/2021/07/TAKE-AWAY-APP-WEB.png" />
                </ImgContainer>
                <InfoContainer>
                    <Title>Yogurt Helado</Title>
                    <Description>
                        Yogurt Helado con 4 salsas a eleccion y baño de
                        chocolate
                    </Description>
                    <Price>$1.500</Price>
                    <FilterContainer>
                        <Filter>
                            <FilterTitle>Sabor</FilterTitle>
                            <FilterFlavor flavor="crimson" />
                            <FilterFlavor flavor="chocolate" />
                            <FilterFlavor flavor="coral" />
                        </Filter>
                        <Filter>
                            <FilterTitle>Tamaño</FilterTitle>
                            <FilterSize>
                                <FilterSizeOption>Grande</FilterSizeOption>
                                <FilterSizeOption>Mediano</FilterSizeOption>
                                <FilterSizeOption>Pequeño</FilterSizeOption>
                            </FilterSize>
                        </Filter>
                    </FilterContainer>
                    <AddContainer>
                        <AmountContainer>
                            <Remove />
                            <Amount>1</Amount>
                            <Add />
                        </AmountContainer>
                        <Button>AGREGAR AL CARRITO</Button>
                    </AddContainer>
                </InfoContainer>
            </Wrapper>
            <Newsletter />
            <Footer />
        </Container>
    );
};

export default Product;
