import {
    FavoriteBorderOutlined,
    LocationSearchingOutlined,
    ShoppingCartOutlined,
} from "@mui/icons-material";
import styled from "@emotion/styled/macro";

const Info = styled.div`
    opacity: 0;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgb(0, 0, 0, 0.2);
    z-index: 3;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: opacity 0.7s, transform 1.5s ease-in-out;
    cursor: pointer;
`;

const Container = styled.div`
    position: relative;
    flex: 1;
    margin: 5px;
    min-width: 280px;
    height: 350px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: lightgreen;
    &:hover ${Info} {
        opacity: 0.5;
        transform: scale(0.8);
    }
`;

const Circle = styled.div`
    width: 200px;
    height: 200px;
    border-radius: 50%;
    background-color: white;
    position: absolute;
`;

const Image = styled.img`
    z-index: 2;
    width: 100%;
`;

const Icon = styled.div`
    width: 40px;
    height: 40px;
    background-color: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px;
    transition: all 0.5s ease;

    &:hover {
        background-color: #4da84d;
        transform: scale(1.5);
    }
`;

const Product = ({ item }) => {
    return (
        <Container>
            <Circle />
            <Image src={item.image} alt={item.name} />
            <Info>
                <Icon>
                    <ShoppingCartOutlined />
                </Icon>
                <Icon>
                    <LocationSearchingOutlined />
                </Icon>
                <Icon>
                    <FavoriteBorderOutlined />
                </Icon>
            </Info>
        </Container>
    );
};

export default Product;
