import styled from "@emotion/styled";
import Announcement from "../components/Announcement";
import Navbar from "../components/Navbar";
import Products from "../components/Products";
import Newsletter from "../components/Newsletter";
import Footer from "../components/Footer";

const Container = styled.div``;
const Title = styled.h1`
    margin: 20px;
`;
const FilterContainer = styled.div`
    display: flex;
    justify-content: space-between;
`;
const Filter = styled.div`
    margin: 20px;
`;
const FilterText = styled.span`
    font-size: 20px;
    font-weight: 600;
    margin-right: 20px;
`;
const Select = styled.select`
    padding: 10px;
    margin-right: 20px;
    border: none;
    font-size: 20px;
    cursor: pointer;
`;
const Option = styled.option``;

const ProductList = () => {
    return (
        <Container>
            <Navbar />
            <Announcement />
            <Title>Toppings</Title>
            <FilterContainer>
                <Filter>
                    <FilterText>Filtra Los Productos</FilterText>
                    <Select>
                        <Option disabled selected>
                            Todos
                        </Option>
                        <Option>Yogurt Helado</Option>
                        <Option>Toppins</Option>
                        <Option>Waffles</Option>
                        <Option>Licuados</Option>
                        <Option>Batidos</Option>
                    </Select>
                    <Select>
                        <Option disabled selected>
                            Tamaño
                        </Option>
                        <Option>Chico</Option>
                        <Option>Mediano</Option>
                        <Option>Grande</Option>
                    </Select>
                </Filter>
                <Filter>
                    <FilterText>Ordena Los Productos</FilterText>
                    <Select>
                        <Option disabled selected>
                            Todos
                        </Option>
                        <Option>Alfabetico</Option>
                        <Option>Precio</Option>
                    </Select>
                </Filter>
            </FilterContainer>
            <Products />
            <Newsletter />
            <Footer />
        </Container>
    );
};

export default ProductList;
