import styled from "@emotion/styled";
import {
    Email,
    Facebook,
    Instagram,
    PhoneAndroid,
    Pinterest,
    Room,
    Twitter,
} from "@mui/icons-material";
import React from "react";

const Container = styled.div`
    display: flex;
`;
const Left = styled.div`
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: 20px;
`;
const Center = styled.div`
    flex: 1;
    padding: 20px;
`;
const Title = styled.h3`
    margin-bottom: 30px;
`;
const List = styled.ul`
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
`;
const ListItem = styled.li`
    width: 50%;
    margin-bottom: 10px;
`;
const Right = styled.div`
    flex: 1;
    padding: 20px;
`;
const ContactItem = styled.div`
    margin-bottom: 20px;
    display: flex;
    align-items: center;
`;
const Payments = styled.img`
    width: 50%;
`;
const Logo = styled.div``;
const Description = styled.div`
    margin: 20px 0;
`;
const SocialContainer = styled.div`
    display: flex;
`;
const SocialIcon = styled.div`
    width: 40px;
    height: 40px;
    border-radius: 50%;
    color: #fff;
    background-color: #${(props) => props.color};
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 20px;
`;

const Footer = () => {
    return (
        <Container>
            <Left>
                <Logo>Vivere Bene</Logo>
                <Description>Los mejores Yogurt Helados</Description>
                <SocialContainer>
                    <SocialIcon color="3b5999">
                        <Facebook />
                    </SocialIcon>
                    <SocialIcon color="e4405f">
                        <Instagram />
                    </SocialIcon>
                    <SocialIcon color="55acef">
                        <Twitter />
                    </SocialIcon>
                    <SocialIcon color="e60023">
                        <Pinterest />
                    </SocialIcon>
                </SocialContainer>
            </Left>
            <Center>
                <Title>Sitios Utilies</Title>
                <List>
                    <ListItem>Home</ListItem>
                    <ListItem>Carta</ListItem>
                    <ListItem>Nutricion</ListItem>
                    <ListItem>Noticias</ListItem>
                    <ListItem>Vida Bene</ListItem>
                    <ListItem>Tiendas</ListItem>
                    <ListItem>Contacto</ListItem>
                    <ListItem>A Domicilio</ListItem>
                </List>
            </Center>
            <Right>
                <Title>Contact</Title>
                <ContactItem>
                    <Room style={{ marginRight: "10px" }} /> Junin, Buenos Aires
                </ContactItem>
                <ContactItem>
                    <PhoneAndroid style={{ marginRight: "10px" }} /> +236
                    444-4444
                </ContactItem>
                <ContactItem>
                    <Email style={{ marginRight: "10px" }} />{" "}
                    viverebene@viverebee.com
                </ContactItem>
                <Payments src="https://i.ibb.co/Qfvn4z6/payments.png" />
            </Right>
        </Container>
    );
};

export default Footer;
